/**
 * 
 * ""
 * ""
 *
 * @param func - 
 * @param wait - 
 * @returns {Function}
 */
const debounce = (func, wait) => {
  let timeout;

  return (...args) => {
    clearTimeout(timeout);

    return new Promise((resolve, reject) => {
      timeout = setTimeout(async () => {
        timeout = null;

        try {
          resolve(await func.apply(this, args));
        } catch (ex) {
          reject(ex);
        }
      }, wait);
    });
  };
};

export default debounce;
