/**
 * 
 * 
 *
 * @param fun - 
 * @param delay - 
 * @return {Function}
 */

export default function throttle(fun, delay) {
  let time;

  return async (...args) => {
    const now = +new Date();

    if (time && now - time > delay) {
      await fun.apply(this, args);
      time = now;
    } else if (!time) {
      await fun.apply(this, args);
      time = now;
    }
  };
}
